import { h, Component } from 'preact';
import { Router, Link } from 'preact-router';
import { connect } from 'preact-redux';
import { mapDispatchToProps } from '../../store/number';

const mapStoreToProps = (state) => ({
  number: state.number,
  test: state.test,
})

@connect(mapStoreToProps, mapDispatchToProps)
export default class Home extends Component {

  addTodos = () => {
    this.props.addNumber(2);
  };

  subTest = () => {
    this.props.addTest(1);
  }

  render(props) {
    return (
      <div>
        <h2>about page</h2>
        <Link activeClassName="active" href="/">go to home page</Link>
        <button onclick={this.addTodos}> Add todo</button>
        <button onclick={this.subTest}> Add test</button>
        {this.props.number}
      </div>
    );
  }
}


